.smallHeader {
  .container {
    height: 443px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: top center;
    text-align: left;
    padding-top: 20px;
    position: relative;
    .site {
      margin: 250px 100px;
      position: absolute;
      top: 0;
      a {
        color: #fff;
        position: relative;
        display: inline-block;
        margin-right: 10px;
        padding-right: 10px;
        font-weight: 600;
        font-size: 18px;
        text-decoration: none;
        background-color: transparent;
        &::after {
          position: absolute;
          content: '/';
          right: -6px;
          top: 0;
          color: #fff;
        }
      }
      span {
        color: #d82a4e;
        display: inline-block;
        margin-right: 10px;
        padding-right: 10px;
        font-weight: 600;
        font-size: 18px;
      }
    }
  }
  .section {
    margin-top: -140px;
    display: block;
    .container {
      max-width: 1180px;
      width: 100%;
      padding-right: 15px;
      padding-left: 15px;
      margin-right: auto;
      margin-left: auto;
      .warp {
        position: relative;
        padding: 60px 0;
        background: #d82a4e;
        .title {
          margin-bottom: 0;
          text-align: center;
          padding: 0 110px;
          color: #fff !important;
          h2 {
            position: absolute;
            width: 100%;
            left: 0;
            bottom: 100%;
            font-size: 24px;
            margin-bottom: 0;
            text-align: center;
            font-weight: 600;
            margin: 0;
            font-family: inherit;
            span {
              display: inline-block;
              background: #d82a4e;
              padding: 20px 60px 10px;
              color: #fff;
            }
          }
        }
        .row {
          display: flex;
          -ms-flex-wrap: wrap;
          flex-wrap: wrap;
          margin-right: -15px;
          margin-left: -15px;
          .col {
            margin-left: 8.333333%;
            flex: 0 0 83.333333%;
            max-width: 83.333333%;
            position: relative;
            width: 100%;
            min-height: 1px;
            padding-right: 15px;
            padding-left: 15px;
            form {
              background-color: transparent;
              padding: 0;
              display: flex;
              justify-content: space-between;
              input {
                height: 56px;
                width: 36.6%;
                padding: 0 30px;
                margin-right: 18px;
                float: left;
                border: none;
                font-size: 14px;
                background: #edf4f6;
                margin: 0;
                font-family: inherit;
                font-size: inherit;
                line-height: inherit;
              }
              button {
                display: inline-block;
                min-width: 196px;
                text-align: center;
                border: none;
                padding: 15px 10px;
                font-weight: 600;
                font-size: 16px;
                position: relative;
                color: #fff;
                cursor: pointer;
                background: #000;
                -webkit-appearance: button;
                text-transform: none;
                overflow: visible;
              }
            }
          }
        }
      }
    }
  }
}
